@use '../base/variables' as v;
@use '../base/mixins' as m;

.galeria{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}

.galeria-imagenes{
    @include m.resetear-lista;
    @include m.grid(2, 2rem);
    @include m.tablet {
        @include m.grid(4, 1.5rem);
    }
    @include m.compu {
        @include m.grid(6, 1rem);
    }

}

.overlay{
    position: fixed; // posicion fija
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / .5);

    picture{

        display: block;
        max-width: 90rem;
    }

}

.btn-cerrar {
 

    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;

    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover {
        cursor: pointer;
    }

}